import React, { Fragment } from 'react';
import Types from 'prop-types';
import AlertCircle from '@transferwise/icons/react/alert-circle';
import Document from '@transferwise/icons/react/document';

const CompleteStep = props => {
  const {
    fileName,
    isComplete,
    isError,
    isImage,
    onClear,
    s3buttonText,
    s3failureText,
    s3successText,
    uploadedImage,
  } = props;
  return (
    <div className="droppable-complete-card droppable-card" aria-hidden={!isComplete}>
      <div className="droppable-card-content ">
        <div>
          {!isError ? (
            <Fragment>
              {isImage && uploadedImage ? (
                <img src={uploadedImage} alt="OK" className="thumbnail " />
              ) : (
                <Document size="xl" />
              )}

              {fileName && (
                <p className="m-b-0">
                  <small>{fileName}</small>
                </p>
              )}
              {s3successText && (
                <div className="caption m-t-1">
                  <h4>{s3successText}</h4>
                </div>
              )}
            </Fragment>
          ) : (
            <Fragment>
              <AlertCircle size="xxl" className="text-danger" />
              {s3failureText && <p className="m-t-2">{s3failureText}</p>}
            </Fragment>
          )}
        </div>
        {s3buttonText && (
          <button className="btn btn-primary btn-sm m-t-3" onClick={() => onClear()}>
            {s3buttonText}
          </button>
        )}
      </div>
    </div>
  );
};

CompleteStep.propTypes = {
  s3buttonText: Types.string.isRequired,
  s3successText: Types.string.isRequired,
  s3failureText: Types.string.isRequired,
  fileName: Types.string.isRequired,
  isComplete: Types.bool.isRequired,
  isError: Types.bool.isRequired,
  isImage: Types.bool.isRequired,
  onClear: Types.func.isRequired,
  uploadedImage: Types.string,
};
CompleteStep.defaultProps = {
  uploadedImage: null,
};

export default CompleteStep;



